import { Injectable, Inject } from '@angular/core';
import {Http, Response, RequestOptions, Headers,URLSearchParams } from '@angular/http';
import { HttpClient, HttpHeaders, HttpParams, HttpRequest } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

@Injectable()
export class ProductSubcategoryMasterService {

  apiUrl: string;

  constructor(
    private http: HttpClient,
    @Inject('API_URL') apiUrl: string
  ) { 
    this.apiUrl = apiUrl + 'product_sub_category'
  }

  
  index(post): Observable<any[]>{
    console.log(post);
    let myHeaders = new HttpHeaders();
    myHeaders.append('Content-Type', 'application/json');
    let Params = new HttpParams();
    for(let key in post){
      if(post.hasOwnProperty(key)){
        Params = Params.append(key, post[key]);
      }
    }
    return this.http.get(this.apiUrl, { headers: myHeaders, params:Params})
    .map(
      (response) => <any[]>response['data'],
      (error) => error
    )

  }

  store(post): Observable<any[]>{
    return this.http.post(this.apiUrl, post)
    .map(
      (response) => <any[]>response,
      (error) => error
    );
  }

  update(id, post): Observable<any[]>{
    return this.http.put(this.apiUrl+'/'+id, post)
    .map(
      (response) => <any[]>response,
      (error) => error
    );
  }

  destroy(id): Observable<any[]>{
    return this.http.delete(this.apiUrl+'/'+id)
    .map(
      (response) => <any[]>response,
      (error) => error
    );
  }

}



