export class Holiday {
    no_of_days:Number;
    code:string;
    name:string;

   constructor(obj?: any) {
       this.no_of_days = obj && obj.no_of_days || null;
    this.code = obj && obj.code || null;
    this.name = obj && obj.name || null;
   }
}



