export class Section {
    code:string;
    name:string;
    area:string;
   // garden_id:string;

   constructor(obj?: any) {
        this.code = obj && obj.code || null;
        this.name = obj && obj.name || null;
        this.area = obj && obj.area || null;
       // this.garden_id = obj && obj.garden_id || null;
   }
}



