export class CostCenter {
    
        code:string;
        remarks:string;
        name:string;
        
       constructor(obj?: any) {
        this.code = obj && obj.code || null;
        this.name = obj && obj.name || null;
        this.remarks = obj && obj.remarks || null;
       }
    }
    


