export class StaffPfLoanMaster {
    date:string;
    amount:string;
    installment_amount:string;
    no_of_installment:string;    
    remarks:string;
   constructor(obj?: any) {
    this.date = obj && obj.date || null;
    this.amount = obj && obj.amount || null;
    this.installment_amount = obj && obj.installment_amount || null;
    this.no_of_installment = obj && obj.no_of_installment || null;    
    this.remarks = obj && obj.remarks || null;
   }
}



