export class PaybookMaster {
    code:string;
    name:string;
    applied_date:string;
  
     
   constructor(obj?: any) {
    this.code = obj && obj.code || null;
    this.name = obj && obj.name || null;
    this.applied_date = obj && obj.applied_date || null;
    
   }
}



