export class BillOfMaterial {
    
    code:string;
    name:string;
    status:string;
    

   constructor(obj?: any) {
    this.code = obj && obj.code || null;
    this.name = obj && obj.name || null;
    this.status = obj && obj.status || null;
    
   }
}



