import { Injectable ,Inject} from '@angular/core';
import { Http, Response, Headers, RequestOptions, URLSearchParams } from '@angular/http';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

@Injectable()
export class StockJournalService {

  apiUrl: string;
  constructor(
    private http: HttpClient,
    @Inject('API_URL') apiUrl: string
  ) { 
    this.apiUrl = apiUrl;
  }
  index(post): Observable<any[]>{
    return this.http.get(this.apiUrl + 'sj', post)
    .map(
      (response) => <any[]>response['data'],
      (error) => error
    )

  }

  all(): Observable<any>{
    return this.http.get(this.apiUrl + 'sj')
    .map(
      (response) => response,
      (error) => error
    )
  }

  show(id){
    return this.http.get(this.apiUrl + 'sj/'+id)
    .map(
      (response) => response,
      (error) => error
    );
  }
  store(post){
    return this.http.post(this.apiUrl + 'sj',post)
    .map(
      (response) => response,
      (error) => error
    );
    
  }
  destroy(id){
    return this.http.delete(this.apiUrl +'sj/'+id).map(
      (response:Response)=> response,
      (error) => error)
  }
  update(id, post): Observable<any[]>{
    return this.http.put(this.apiUrl + 'sj/'+id,post)
    .map(
      (response) => <any[]>response,
      (error) => error
    );
  }

}



