import { Injectable } from '@angular/core';
import { CanActivate, CanActivateChild, Router, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { JwtHelper } from 'angular2-jwt';
// import { AppDataService } from '../../shared';

@Injectable()
export class AuthGuard implements CanActivate, CanActivateChild {

    jwtHelper: JwtHelper = new JwtHelper();

    constructor(
        private router: Router,
        // private dataService: AppDataService
    ) { }

    canActivate() {
        let result = this.checkUser();
        return result;
    }

    canActivateChild(){
        // console.log('Inside Can Activate Child Auth Guard');
        let result = this.checkUser();
        return result;
    }

    checkUser(){
        let currentToken = JSON.parse(localStorage.getItem('teaERPToken')) || null;
        let currentUser = JSON.parse(localStorage.getItem('teaERPUser')) || null ;
        let currentEstate = JSON.parse(localStorage.getItem('teaERPEstate')) || null;
        if(currentToken && currentToken['token'] && this.jwtHelper.isTokenExpired(currentToken['token'])){
            console.log('Token Expired check in Auth Guard.');
            this.removeData();
            this.router.navigate(['/login'], { queryParams: { type:'error', msg: 'Token Expired Please login to continue' } });
            return false;
        }
        if (currentToken && currentToken['token'] && currentUser && currentUser['email'] && currentEstate && currentEstate['estate']) {
            return true;
        }
        this.removeData();
        this.router.navigate(['/login']);
        return false;
    }

    removeData(){
        localStorage.removeItem('teaERPToken');
        localStorage.removeItem('teaERPEstate');
        localStorage.removeItem('teaERPUser');
        localStorage.removeItem('teaERPPerms');
    }
}



