export class Route {

    name: string;
    method: string;
    path: string;
    action_name: string;
    permission_id: number;

    constructor(obj?: any) {
        this.name = obj && obj.name || null;
        this.method = obj && obj.method || null;
        this.path = obj && obj.path || null;
        this.action_name = obj && obj.action_name || null;
        this.permission_id = obj && obj.permission_id || null;
    }
}



