export class EmployeePaybook {
    code:string;
    name:string;
    min_task:number;
    min_rate:number;
    ot_rate:number;
    applied_date:number;
    //ot_task_flag
    //status
    //active

   constructor(obj?: any) {
       this.code = obj && obj.code || null;
       this.name = obj && obj.name || null;
       this.min_task = obj && obj.min_task || null;
       this.min_rate = obj && obj.min_rate || null;
       this.ot_rate = obj && obj.ot_rate || null;
       this.applied_date = obj && obj.applied_date || null;
   }
}


