export class Supplier {
    name:string;
    address:string;
    contact_no:number;
    type:string;

   constructor(obj?: any) {
       this.name = obj && obj.name || null;
       this.address = obj && obj.address || null;
       this.contact_no = obj && obj.contact_no || null;
       this.type = obj && obj.type || null;
   }
}



