export class GensetUnitConsumption {
    
    date:string;
    running_date:string;
    hsd_unit_consumption:string;
    electricity_unit_consumed:string;
    

   constructor(obj?: any) {
    this.date = obj && obj.date || null;
    this.running_date = obj && obj.running_date || null;
    this.hsd_unit_consumption = obj && obj.hsd_unit_consumption || null;
    this.electricity_unit_consumed = obj && obj.electricity_unit_consumed || null;
    
   }
}



