export class PruneStyle {
    code:string;
    name:string;
    pruning_interval:string;

   constructor(obj?: any) {
       this.code = obj && obj.code || null;
       this.name = obj && obj.name || null;
       this.pruning_interval = obj && obj.pruning_interval || null;
   }
}



