export class ActivityMaster {
    code:string;
    name:string;
    minimum_task:number;
    
   constructor(obj?: any) {
       this.code = obj && obj.code || null;
       this.name = obj && obj.name || null;
       this.minimum_task = obj && obj.minimum_task || null;
      
   }
}



