import { Injectable ,Inject} from '@angular/core';
import { HttpClient,HttpParams,HttpHeaders } from '@angular/common/http';
import { Http, Response, Headers, RequestOptions, URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

@Injectable()
export class StoreTransactionService {
  
  apiUrl: string;
  constructor(
    private http: HttpClient,
    @Inject('API_URL') apiUrl: string
  ) { 
    this.apiUrl = apiUrl;
  }
  index(post): Observable<any[]>{
    let myHeaders = new HttpHeaders();
    myHeaders.append('Content-Type', 'application/json');
    let Params = new HttpParams();
    for(let key in post){
      if(post.hasOwnProperty(key)){
        Params = Params.append(key, post[key]);
      }
    }
    return this.http.get(this.apiUrl + 'store_transaction',{headers:myHeaders,params:Params})
    .map(
      (response) => <any[]>response['data'],
      (error) => error
    )
  }

  store(post){
    return this.http.post(this.apiUrl + 'store_transaction',post)
    .map(
      (response) => response,
      (error) => error
    );
    
  }
  destroy(id){
    return this.http.delete(this.apiUrl +'store_transaction/'+id).map(
      (response:Response)=> response,
      (error) => error)
  }
  update(id, post): Observable<any[]>{
    return this.http.put(this.apiUrl + 'store_transaction/'+id,post)
    .map(
      (response) => <any[]>response,
      (error) => error
    );
  }


}



