export class Item {
    code:string;
    name:string;
    description:string;
    minimum_stock:number;
    maximum_stock:number;
   // quantity:number;
    reorder_quantity:number;
    
    

   constructor(obj?: any) {
    this.code = obj && obj.code || null;
    this.name = obj && obj.name || null;
    this.description = obj && obj.description || null;
    this.minimum_stock = obj && obj.minimum_stock || null;
    this.maximum_stock = obj && obj.maximum_stock || null;
 //   this.quantity = obj && obj.quantity || null;
    this.reorder_quantity = obj && obj.reorder_quantity || null;
    
   }
}



