import { Component } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.scss']
})
export class AppComponent {
    constructor(private translate: TranslateService) {
        translate.addLangs(['en', 'fr', 'ur', 'es', 'it', 'fa']);
        translate.setDefaultLang('en');
        const browserLang = translate.getBrowserLang();
        translate.use(browserLang.match(/en|fr|ur|es|it|fa/) ? browserLang : 'en');

        // "@ng-bootstrap/ng-bootstrap": "^1.0.0-beta.4",
        
        //Remove string.fromCharCode line of code from jqxgrid.js
        console.log(String.fromCharCode(119,119,119,46,106,113,119,105,100,103,101,116,115,46,99,111,109));
    }

    
}



