import { Injectable, Inject } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map';

@Injectable()
export class AuthenticateService {

  public token: string;
  apiUrl: string;
  
    constructor(
      private http: HttpClient,
      @Inject('API_URL') apiUrl:string
    
    ) {
      this.apiUrl = apiUrl;
      //set token if saved in localstorage
      let currentUser = JSON.parse(localStorage.getItem('teaERPToken'));
      this.token = currentUser && currentUser.token;
     }

    getToken(): string {
      let currentUser = JSON.parse(localStorage.getItem('teaERPToken'));
      let currentToken = currentUser && currentUser.token || null;
      return currentToken;
    }

    getUser(): any {
      let currentUser = JSON.parse(localStorage.getItem('teaERPUser'));
      return currentUser;
    }

    getEstate(): any{
      let currentEstate = JSON.parse(localStorage.getItem('teaERPUser'));
      currentEstate = currentEstate['estate'];
      return currentEstate;
    }
  
     login(post): Observable<any[]>{
       return this.http.post(this.apiUrl+'authenticate', post)
       .map((response) => {
          console.log(response);
          let token = response && response['token'];
          if(token){
            this.token = token;
            localStorage.setItem('teaERPToken',JSON.stringify({token:token}));
            // return true;
          }else{
            // return false;
          }
          return <any[]>response;
       },
       (error: Error) => error);
     }
  
     logout(): void{
       this.token = null;
       localStorage.removeItem('teaERPToken');
       localStorage.removeItem('teaERPUser');
       localStorage.removeItem('teaERPEstate');
     }

     getDetail(post): Observable<any[]>{
        let myHeaders = new HttpHeaders();
        myHeaders.append('Content-Type', 'application/json');
        let Params = new HttpParams();
        for(let key in post){
          if(post.hasOwnProperty(key)){
            Params = Params.append(key, post[key]);
          }
        }
        return this.http.get(this.apiUrl + 'authenticate',{headers:myHeaders,params:Params})
        .map(
          (response) => <any[]>response,
          (error) => error
        )
     }
  

}




