export class Store {
        code:string;
        name:string;
        address:string;
       contact_no:string;
       store_keeper:string;
       
       constructor(obj?: any) {
        this.code = obj && obj.code || null;
        this.name = obj && obj.name || null;
        this.address = obj && obj.address || null;
        this.contact_no = obj && obj.contact_no || null;
        this.store_keeper = obj && obj.store_keeper || null;
       }
    }
    


