import { Injectable } from '@angular/core';
import { DatePipe, DecimalPipe } from '@angular/common';
import { utils, write, WorkBook } from 'xlsx';
import { saveAs } from 'file-saver';

@Injectable()
export class FileExportService {

  constructor(private datePipe: DatePipe,private decimalPipe: DecimalPipe) { }

  /**
   * Function to Get Time difference
   * @param format 
   */
  getTimeDifference(startDate:any, endDate:any) {
    //Get 1 day in milliseconds
    let one_day = 1000 * 60 * 60 * 24;

    // Convert both dates to milliseconds
    let date1_ms = new Date(startDate).getTime();
    let date2_ms = new Date(endDate).getTime();

    // Calculate the difference in milliseconds
    let difference_ms = date2_ms - date1_ms;
    //take out milliseconds
    // difference_ms = difference_ms / 1000;
    let result = difference_ms/3600000;
    // let seconds = Math.floor(difference_ms % 60);
    // difference_ms = difference_ms / 60;
    // let minutes = Math.floor(difference_ms % 60);
    // difference_ms = difference_ms / 60;
    // let hours = Math.floor(difference_ms % 24);
    // let days = Math.floor(difference_ms / 24);
    // return days!=0 ? days + ' days, ' + hours + '.' + minutes : hours + '.' + minutes;
    return result;
  }

  /**
   * Function to Get Current Date Time to certain format
   * @param format 
   */
  getDateTime(format: string = 'medium') {
    return this.datePipe.transform(new Date(), format);
  }

  /**
   * Function to Get formated decimal number
   * @param format 
   */
  getDecimal(value,minFractionDigits:string) {
    return this.decimalPipe.transform(value,minFractionDigits);
  }

  /**
   * Function to Convert given Date
   * @param date 
   * @param format 
   */
  convertDate(date: string, format: string = 'medium') {
    return this.datePipe.transform(new Date(date), format);
  }


  /**
   * Function to Concat Give name with Current Date
   * @param fileName 
   */
  getFileName(fileName: string) {
    let currentDate: string = this.getDateTime('shortDate') + ' ' + this.getDateTime('shortTime');
    currentDate = currentDate.toLowerCase();
    currentDate = currentDate.replace(/[^a-zA-Z0-9]+/g, '-');
    return fileName + '-' + currentDate;
  }

  /**
   * Function to export Data in excel sheet using the provided data & file Name
   * @param data 
   * @param fileName 
   */
  downloadFile(data: any, fileName: string) {
    const ws_name = 'SomeSheet';
    const wb: WorkBook = { SheetNames: [], Sheets: {} };
    const ws: any = utils.json_to_sheet(data);
    wb.SheetNames.push(ws_name);
    wb.Sheets[ws_name] = ws;
    const wbout = write(wb, { bookType: 'xlsx', bookSST: true, type: 'binary' });

    saveAs(new Blob([this.s2ab(wbout)], { type: 'application/octet-stream' }), fileName + '.xlsx');
  }

  /**
   * Function to save files on the client-side with the provided excel sheet
   * @param s 
   */
  private s2ab(s) {
    const buf = new ArrayBuffer(s.length);
    const view = new Uint8Array(buf);
    for (let i = 0; i !== s.length; ++i) {
      view[i] = s.charCodeAt(i) & 0xFF;
    };
    return buf;
  }

  getMultiplication(a, b){
    return a * b;
  }

  SaveFile(data,fileName){
    saveAs(data,fileName);
  }

}



