export class InventorySetup {
    //fiscal_year_id:string;
    purchase_order_expiry:boolean;
    auto_generate_req_no:boolean;
    auto_generate_grn_no:boolean;
    auto_generate_missue_no:boolean;
    auto_generate_po_no:boolean;
    pre_req_no:string;
    suf_req_no:string;
    last_req_no:string;
    pre_po_no:string;
    suf_po_no:string;
    last_po_no:string;
    pre_grn_no:string;
    suf_grn_no:string;
    last_grn_no:string;
    pre_missue_no:string;
    suf_missue_no:string;
    last_missue_no:string;
    validation_method:string;
    
    
   constructor(obj?: any) {
   //this.fiscal_year_id = obj && obj.fiscal_year_id || null;
    this.purchase_order_expiry = obj && (obj.purchase_order_expiry==1)?true:false || null;
    this.auto_generate_req_no = obj && (obj.auto_generate_req_no==1)?true:false || null;
    this.auto_generate_grn_no = obj && (obj.auto_generate_grn_no==1)?true:false || null;
     this.auto_generate_missue_no = obj && (obj.auto_generate_missue_no==1)?true:false || null;
    this.auto_generate_po_no = obj && (obj.auto_generate_po_no==1)?true:false || null;
    this.pre_req_no = obj && obj.pre_req_no || null;
    this.suf_req_no = obj && obj.suf_req_no || null;
    this.last_req_no = obj && obj.last_req_no || null;
    this.pre_po_no = obj && obj.pre_po_no || null;
    this.suf_po_no = obj && obj.suf_po_no || null;
    this.last_po_no = obj && obj.last_po_no || null;
    this.pre_grn_no = obj && obj.pre_grn_no || null;
    this.suf_grn_no = obj && obj.suf_grn_no || null;
    this.last_grn_no = obj && obj.last_grn_no || null;
    this.pre_missue_no = obj && obj.pre_missue_no || null;
    this.suf_missue_no = obj && obj.suf_missue_no || null;
    this.last_missue_no = obj && obj.last_missue_no || null;
    this.validation_method = obj && obj.validation_method || null;
   }
}



