export class FiscalYear {
    name:string;
    start_date:string;
    end_date:string;
   // current:string;
    //user_id:string;

   constructor(obj?: any) {
       this.name = obj && obj.name || null;
       this.start_date = obj && obj.start_date || null;
       this.end_date = obj && obj.end_date || null;
      // this.current = obj && obj.current || null;
   }
}



