import { Injectable,Inject } from '@angular/core';
import { HttpClient,HttpParams,HttpHeaders } from '@angular/common/http';
// import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

@Injectable()
export class StoreService {
  apiUrl: string;
  
    constructor(
      private http: HttpClient,
      @Inject('API_URL') apiUrl: string
    ) {
      this.apiUrl = apiUrl;
  
    }
  
    index(post): Observable<any[]>{
      let myHeaders = new HttpHeaders();
      myHeaders.append('Content-Type', 'application/json');
      let Params = new HttpParams();
      for(let key in post){
        if(post.hasOwnProperty(key)){
          Params = Params.append(key, post[key]);
        }
      }
      return this.http.get(this.apiUrl + 'store',{headers:myHeaders,params:Params})
      .map(
        (response) => <any[]>response['data'],
        (error) => error
      )
  
    }
    add(post){
      return this.http.post(this.apiUrl + 'store',post)
      .map(
        (response) => response,
        (error) => error
      );
      
    }
    destroy(code): Observable<any[]>{
      console.log(code);
      return this.http.delete(this.apiUrl + 'store/'+code)
        .map(
          (response) => <any[]>response,
          (error) => error
        );
    }
      update(code, post): Observable<any[]>{
      return this.http.put(this.apiUrl + 'store/'+code,post)
      .map(
        (response) => <any[]>response,
        (error) => error
      );
    }

}



