
export class PluckingMandaysCalculation {
    start_date:string;
    end_date:string;
    budgeted_date:string;
    yrly_tea_made:number;
    yrly_recovery_percentage:number;
    yrly_green_leaf:number;
    yrly_production_per_plucker:number;
    yrly_mandays:number;
    
       constructor(obj?: any) {
        this.budgeted_date = obj && obj.budgeted_date || null;
        this.yrly_tea_made = obj && obj.yrly_tea_made || null;
        this.yrly_recovery_percentage = obj && obj.yrly_recovery_percentage || null;
        this.yrly_green_leaf = obj && obj.yrly_green_leaf || null;
        this.yrly_production_per_plucker = obj && obj.yrly_production_per_plucker || null;
        this.yrly_mandays = obj && obj.yrly_mandays || null;
        this.start_date = obj && obj.start_date || null;
        this.end_date = obj && obj.end_date || null;
       }
    }
    


