export class ProductionStock {
    lot_no:number;
    processing_date:string;
    description:string;
    
    
  
   constructor(obj?: any) {
    this.lot_no = obj && obj.lot_no || null;
    this.processing_date = obj && obj.processing_date || null;
    this.description = obj && obj.description || null;
    
   }
  }
  
  


