import { Injectable } from '@angular/core';
import { Router, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { HttpEvent, HttpHandler, HttpInterceptor, HttpRequest, HttpHeaders, HttpXsrfTokenExtractor, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { JwtHelper } from 'angular2-jwt';
// import { AppDataService } from '../../shared';

@Injectable()
export class TfmHttpInterceptorService implements HttpInterceptor{

  jwtHelper: JwtHelper = new JwtHelper();

  constructor(
    private router: Router,
    private tokenExtractor: HttpXsrfTokenExtractor,
    // private dataService: AppDataService
    // private currentUserService: CurrentUserService
  ) { }
  
  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    //   console.log(JSON.stringify(req));

      // const headerName = 'X-XSRF-TOKEN';
      // let xsrftoken = this.tokenExtractor.getToken() as string;
      // console.log(xsrftoken);
      // if (xsrftoken !== null && !req.headers.has(headerName)) {
      //   req = req.clone({ headers: req.headers.set(headerName, xsrftoken) });
      // }
      // console.log(req);
      // const token: string = this.currentUserService.token;
      let currentUser = JSON.parse(localStorage.getItem('teaERPToken'));
      const token: string = currentUser && currentUser.token || null;
      if(token && this.jwtHelper.isTokenExpired(token)){
        // console.log(this.jwtHelper.decodeToken(token));
          // console.log('Token Expired check in Interceptor.');
          localStorage.removeItem('teaERPToken');
          localStorage.removeItem('teaERPUser');
          localStorage.removeItem('teaERPEstate');
          this.router.navigate(['/login'],{ queryParams: { type:'error', msg: 'Token Expired Please login to continue' } });
          // return false;
      }

      let currentEstate = JSON.parse(localStorage.getItem('teaERPEstate'));
      const estateid = currentEstate && currentEstate.estate || null;
      let newParams = new HttpParams({fromString: req.params.toString()});
      // console.log(newParams);
      if (token) {

          // previous code.
          // let Request = req;
          // Request.params.append('estate', estateid);
          // Request.headers.append('Authorization', 'Bearer ' + token);

          // console.log(Request);
          req = req.clone({ headers: req.headers.set('Authorization', 'Bearer ' + token), params: req.params.set('estate',estateid) });
          
          // req = req.clone({ });

          // req = req.clone({
          //   setHeaders: {
          //     Authorization: `Bearer token`
          //   },
          //   setParams:{
          //     estate: estateid
          //   },
          // });
          // console.log(req);
      }

      // if (!req.headers.has('Content-Type')) {
      //     req = req.clone({ headers: req.headers.set('Content-Type', 'application/json') });
      // }
      // console.log(req);

      // req = req.clone({ headers: req.headers.set('Accept', 'application/json') });
      
      return next.handle(req);
  }

}



