export class StoreTransaction {
    quantity:number;
    rate:number;
    value:number;
    total:number;
    remarks:string;

   constructor(obj?: any) {
       this.quantity = obj && obj.quantity || null;
       this.rate = obj && obj.rate || null;
       this.value = obj && obj.value || null;
       this.total = obj && obj.total || null;
       this.remarks = obj && obj.remarks || null;
   }
}



