export class ProductSubcategory {
    code:string;
    name:string;
    category_id:number;
    
   constructor(obj?: any) {
    this.code = obj && obj.code || null;
    this.name = obj && obj.name || null;
    this.category_id = obj && obj.category_id || null;
   }
}



