export class DeductionMaster {
    code:string;
    percentage:string;
    check_percentage:boolean;
    amount:string;
    name:string;

   constructor(obj?: any) {
       this.code = obj && obj.code || null;
       this.percentage = obj && obj.percentage || null;
       this.check_percentage = obj && obj.check_percentage || null;
       this.amount = obj && obj.amount || null;
       this.name = obj && obj.name || null;

   }
}



