export class Estate {
    code:string;
    name:string;
    address:string;
    contact_no:string;
    
    
   constructor(obj?: any) {
       this.code = obj && obj.code || null;
       this.name = obj && obj.name || null;
       this.address = obj && obj.address || null;
       this.contact_no = obj && obj.contact_no || null;
       
      
   }
}



