export class User {

    username: string;
    email: string;
    password: string;
    password_confirmation: string;

    constructor(obj?: any) {
        this.username = obj && obj.username || null;
        this.email = obj && obj.email || null;
        this.password = obj && obj.password || null;
        this.password_confirmation = obj && obj.password_confirmation || null;
    }
}


