export class DirectGrn {
    code:string;
    direct_grn_no:number;
    remarks:string;
    cr_account_head_id:number;
    date:string;
    // receipt_no:number;
    dr_account_head:string;
    dr_account_sub_head:string;
    cr_account_head:string;
    cr_account_sub_head:string;
    bill_code:string;
    bill_no:number;

   constructor(obj?: any) {
    this.code = obj && obj.code || null;
    this.direct_grn_no = obj && obj.direct_grn_no || null;
    this.remarks = obj && obj.remarks || null;
    this.cr_account_head = obj && obj.cr_account_head || null;
    this.date = obj && obj.date || null;
    // this.receipt_no = obj && obj.receipt_no || null;
    this.dr_account_head = obj && obj.dr_account_head || null;
    this.dr_account_sub_head = obj && obj.dr_account_sub_head || null;
    this.cr_account_head_id = obj && obj.cr_account_head_id || null;
    this.cr_account_sub_head = obj && obj.cr_account_sub_head || null;
    this.bill_code = obj && obj.bill_code || null;
    this.bill_no = obj && obj.bill_no || null;
    
   }
}



