import { Injectable ,Inject} from '@angular/core';
import { Http, Response, Headers, RequestOptions, URLSearchParams } from '@angular/http';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

@Injectable()
export class DirectPoMasterService {apiUrl: string;
  constructor(
    private http: HttpClient,
    @Inject('API_URL') apiUrl: string
  ) { 
    this.apiUrl = apiUrl;
  }
  index(post): Observable<any[]>{
    // let myHeaders = new Headers();
    // myHeaders.append('Content-Type', 'application/json');
    // let params = new URLSearchParams();
    // for(let key in post){
    //   if(post.hasOwnProperty(key)){
    //     params.set(key, post[key])
    //   }
    // }

    // let options = new RequestOptions({ headers: myHeaders, params:params});
    return this.http.get(this.apiUrl + 'direct_po', post)
    .map(
      (response) => <any[]>response['data'],
      (error) => error
    )

  }
  store(post){
    return this.http.post(this.apiUrl + 'direct_po',post)
    .map(
      (response) => response,
      (error) => error
    );
    
  }
  destroy(id){
    return this.http.delete(this.apiUrl +'direct_po/'+id).map(
      (response:Response)=> response,
      (error) => error)
  }
  update(id, post): Observable<any[]>{
    return this.http.put(this.apiUrl + 'direct_po/'+id,post)
    .map(
      (response) => <any[]>response,
      (error) => error
    );
  }

}



