import { Injectable,Inject } from '@angular/core';
import { Http, Response, Headers, RequestOptions, URLSearchParams } from '@angular/http';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

@Injectable()
export class VendorService {
  apiUrl: string;
  
    constructor(
      private http: HttpClient,
      @Inject('API_URL') apiUrl: string
    ) {
      this.apiUrl = apiUrl;
  
    }
  
    index(post): Observable<any[]>{
   
      return this.http.get(this.apiUrl + 'vendor', post)
      .map(
        (response) => <any[]>response['data'],
        (error) => error
      )
  
    }
    add(post){
      return this.http.post(this.apiUrl + 'vendor',post)
      .map(
        (response) => response,
        (error) => error
      );
      
    }
    destroy(code): Observable<any[]>{
      console.log(code);
      return this.http.delete(this.apiUrl + 'vendor/'+code)
        .map(
          (response) => <any[]>response,
          (error) => error
        );
    }
      update(code, post): Observable<any[]>{
      return this.http.put(this.apiUrl + 'vendor/'+code,post)
      .map(
        (response) => <any[]>response,
        (error) => error
      );
    }

}



