import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute, NavigationStart } from "@angular/router";

@Component({
  selector: "app-quickaccess",
  templateUrl: "./quickaccess.component.html",
  styleUrls: ["./quickaccess.component.scss"]
})
export class QuickaccessComponent implements OnInit {
  public CurrentPath: string;

  links: any = [];

  searchText: string = "";

  constructor(private router: Router, private activatedRoute: ActivatedRoute) {
    this.router = router;
    // this.CurrentPath = this.router.parseUrl(
    //   this.router.url
    // ).root.children.primary.segments[0].path;
  }

  ngOnInit() {
    this.accessType(this.router.parseUrl(this.router.url).root.children.primary.segments[1].path);
    this.getCurrentLinks();
  }

  getCurrentLinks() {
    let currentModule: string = this.CurrentPath;

    switch (currentModule) {
      case "master": {
        this.links = [
          {
            name: "Product Category Master",
            path: "/inventory/product-category-master"
          },
          {
            name: "Chart Of Items",
            path: "/inventory/item-master"
          },
          {
            name: "Vendor Master",
            path: "/inventory/vendor"
          },
          {
            name: "Unit Master",
            path: "/inventory/unit-master"
          },
          {
            name: "Bill Of Material",
            path: "/inventory/bill-of-material"
          },
          {
            name: "Bill Sundry",
            path: "/accounting/bill-sundry-master"
          }
        ];
        break;
      }
      case "transaction": {
        this.links = [
          {
            name: "Store Items (Stock)",
            path: "/inventory/store-transaction"
          },
          {
            name: "Requisition/Indents",
            path: "/inventory/requisition-detail-master"
          },
          {
            name: "Import Good Receipt Note",
            path: "/inventory/te-direct-grn"
          },
          {
            name: "Good Receipt Note",
            path: "/inventory/grn"
          },
          {
            name: "Direct DRN",
            path: "/inventory/direct-grn"
          },
          {
            name: "Purchase Order(Req)",
            path: "/inventory/po-requisition-detail-master"
          },
          {
            name: "Material Issue",
            path: "/inventory/material-issue"
          },
          {
            name: "Production Voucher",
            path: "/inventory/production-voucher"
          },
          {
            name: "Stock Journal",
            path: "/inventory/stock-journal"
          },
          {
            name: "Sales",
            path: "/inventory/sales"
          },
          {
            name: "Store Transfer",
            path: "/inventory/store-transfer"
          },
          {
            name: "Purchase Return",
            path: "/inventory/purchase-return"
          },
          {
            name: "Issue Return",
            path: "/inventory/issue-return"
          },
          {
            name: "Sales Return",
            path: "/inventory/sales-return"
          }
        ];
        break;
      }
      case "reports": {
        this.links = [
          {
            name: "Cost Of Materials",
            path: "/reports/cost-of-materials"
          },
          {
            name: "Stock Report",
            path: "/reports/store-ledger-report"
          },
          {
            name: "Stock Ledger",
            path: "/reports/stock-ledger-report"
          },
          {
            name: "Opening Report",
            path: "/reports/opening-report"
          },
          {
            name: "Current Fifo Report",
            path: "/reports/closing-fifo-report"
          },
          {
            name: "Daily Store Log",
            path: "/reports/daily-store-log-report"
          },
          {
            name: "Store Items (Stock)",
            path: "/inventory/store-transaction"
          },
          {
            name: "Goods Receipt Report",
            path: "/reports/goods-receipt-report"
          },
          {
            name: "Material Issue Report",
            path: "/reports/material-issue-report"
          },
          {
            name: "PO (with Requisition)",
            path: "/reports/po-with-requisition-report"
          },
          {
            name: "PO Direct",
            path: "/reports/po-direct"
          }
        ];
        break;
      }
      case "setup": {
        this.links = [
          {
            name: "Estate",
            path: "/setup/estate"
          },
          {
            name: "Fiscal Year",
            path: "/inventory/fiscal-year"
          },
          {
            name: "Inventory Setup",
            path: "/inventory/inventory-setup"
          },
          {
            name: "Store Master",
            path: "/inventory/store"
          }
        ];
        break;
      }
      case "utility": {
        this.links = [
          {
            name: "Manage Users",
            path: "/user-master/users"
          },
          {
            name: "Manage Roles",
            path: "/user-master/roles"
          },
          {
            name: "Manage Permissions",
            path: "/user-master/permissions"
          }
        ];
        break;
      }
      default: {
        console.log("Invalid choice");
        break;
      }
    }
  }


  private accessType(path): void {
    if (
      path == "product-category-master" ||
      path == "item-master" ||
      path == "vendor" ||
      path == "unit-master" ||
      path == "bill-of-material" ||
      path == "bill-sundry-master"
    ) {
      this.CurrentPath = "master";
    } else if (
      path == "store-transaction" ||
      path == "requisition-detail-master" ||
      path == "te-direct-grn" ||
      path == "grn" ||
      path == "direct-grn" ||
      path == "po-requisition-detail-master" ||
      path == "material-issue" ||
      path == "production-voucher" ||
      path == "stock-journal" ||
      path == "sales" ||
      path == "store-transfer" ||
      path == "purchase-return" ||
      path == "issue-return" ||
      path == "sales-return"
    ) {
      this.CurrentPath = "transaction";
    } else if (
      path == "cost-of-materials" ||
      path == "store-ledger-report" ||
      path == "stock-ledger-report" ||
      path == "opening-report" ||
      path == "closing-fifo-report" ||
      path == "daily-store-log-report" ||
      path == "store-transaction" ||
      path == "goods-receipt-report" ||
      path == "material-issue-report" ||
      path == "po-with-requisition-report" ||
      path == "po-direct"
    ) {
      this.CurrentPath = "reports";
    } else if (
      path == "estate" ||
      path == "fiscal-year" ||
      path == "inventory-setup" ||
      path == "store"
    ) {
      this.CurrentPath = "setup";
    } else if (path == "users" || path == "roles" || path == "permissions") {
      this.CurrentPath = "utility";
    }
  }
}



