export class AccountHead {
    
        code:string;
        name:string;
        parent_id:string;
        mailing_name:string;
        mailing_address:string;
        mailing_country:string;
        mailing_estate:string;
         pan_no:string;
        // ledger:string;
        // sub_ledger:string;
        // editable:string;
        

       constructor(obj?: any) {
        this.code = obj && obj.code || null;
        this.name = obj && obj.name || null;
        this.parent_id = obj && obj.parent_id || null;
        this.mailing_name = obj && obj.mailing_name || null;
        this.mailing_address = obj && obj.mailing_address || null;
        this.mailing_country = obj && obj.mailing_country || null;
        this.mailing_estate = obj && obj.mailing_estate || null;
        this.pan_no = obj && obj.pan_no || null;
        // this.ledger = obj && obj.ledger || null;
        // this.sub_ledger = obj && obj.sub_ledger || null;
        // this.editable = obj && obj.editable || null;
        
       }
    }
    


