export class GensetMaster {
    
    code:string;
    manufacturer:string;
    name:string;
    installation_date:string;
    
   constructor(obj?: any) {
    this.code = obj && obj.code || null;
    this.name = obj && obj.name || null;
    this.manufacturer = obj && obj.manufacturer || null;
    this.installation_date = obj && obj.installation_date || null;
   }
}



