export class TeaDispatch {
    invoice_no: number;
    invoice_date: string;
    vehicle_no: string;
    serial_no: string;
    from_station: string;
    destination: string;
    exemption_notification_no: string;
    gate_pass_no: string;
    challan_no: string;
    dispatch_description: string;
    consignee_address: string;
    consignee_phone: number;

   constructor(obj?: any) {
       this.invoice_no = obj && obj.invoice_no || null;
       this.invoice_date = obj && obj.invoice_date || null;
       this.vehicle_no = obj && obj.vehicle_no || null;
       this.serial_no = obj && obj.serial_no || null;
       this.from_station = obj && obj.from_station || null;
       this.destination = obj && obj.destination || null;
       this.exemption_notification_no = obj && obj.exemption_notification_no || null;
       this.gate_pass_no = obj && obj.gate_pass_no || null;
       this.challan_no = obj && obj.challan_no || null;
       this.dispatch_description = obj && obj.dispatch_description || null;
       this.consignee_address = obj && obj.consignee_address || null;
       this.consignee_phone= obj && obj.consignee_phone || null;
   }
}



