export class GensetDailyLog {
    
    date:string;
    start_time:string;
    stop_time:string;
    hsd_consumption:string;
    start_unit:string;
    stop_unit: string;
    rate:number;
    
   constructor(obj?: any) {
    this.date = obj && obj.date || null;
    this.start_time = obj && obj.start_time || null;
    this.stop_time = obj && obj.stop_time || null;
    this.hsd_consumption = obj && obj.hsd_consumption || null;
    this.start_unit = obj && obj.start_unit || null;
    this.stop_unit = obj && obj.stop_unit || null;
    this.rate = obj && obj.rate || null;
    
   }
}



