import { Injectable ,Inject} from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Http, Response, Headers, RequestOptions, URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

@Injectable()
export class JournalVoucherFormService {
    apiUrl: string;
    constructor(
      private http: HttpClient,
      @Inject('API_URL') apiUrl: string
    ) { 
      this.apiUrl = apiUrl;
    }
    index(post): Observable<any[]>{
      // let myHeaders = new Headers();
      // myHeaders.append('Content-Type', 'application/json');
      // let params = new URLSearchParams();
      // for(let key in post){
      //   if(post.hasOwnProperty(key)){
      //     params.set(key, post[key])
      //   }
      // }
  
      // let options = new RequestOptions({ headers: myHeaders, params:params});
      return this.http.get(this.apiUrl + 'journal_voucher_form', post)
      .map(
        (response) => <any[]>response['data'],
        (error) => error
      )
  
    }
    store(post){
      return this.http.post(this.apiUrl + 'journal_voucher_form',post)
      .map(
        (response) => response,
        (error) => error
      );
      
    }
    show(id){
      return this.http.get(this.apiUrl +'journal_voucher_form/'+id).map(
        (response)=> response,
        (error) => error
      );
    }
  
    destroy(id){
      return this.http.delete(this.apiUrl +'journal_voucher_form/'+id).map(
        (response)=> response,
        (error) => error
      );
    }
    update(id, post): Observable<any[]>{
      return this.http.put(this.apiUrl + 'journal_voucher_form/'+id,post)
      .map(
        (response) => <any[]>response,
        (error) => error
      );
    }

}


