import { Injectable, Inject } from '@angular/core';
import { Observable } from 'rxjs';

@Injectable()
export class ErrorHandleService {

  public online: boolean = true;

  constructor() {

  }

    online$ = Observable.fromEvent(window, 'online').subscribe(
        x => {
            this.online = true;
            // this.newtworkWindow.close();
        }
    );

    offline$ = Observable.fromEvent(window, 'offline').subscribe(
        x => {
            this.online = false;
            // this.newtworkWindow.setTitle('NETWORK ERROR');
            // this.newtworkWindow.open();
        }
    );

  formatError(response){
    let message:any = [];
    let result;
    console.log(response);
    if(response['error'] && response['error']['message'] && response['error']['message'].isArray){
      result = false;
      let errMessage = response['error']['message'];
      for(let obj in errMessage){
        for(let objc of errMessage[obj]){
          message.push(objc);
        }
      }
    }else if(response['error'] && response['error']['message']){
      result = false;
      message.push(response['error']['message']);
    }

    if(response['error'] && response['error']['error']){
      message.push(response['error']['error']['message']);
      result = false;
    }

    if(response['message'] && !response['error']){
      message.push(response['message']);
      result = true;
    }

    if(response['message'] && response['error']){
      message = '';
      message = 'Internal Server Error';
      result = false;
    }

    if(!this.online){
      message = '';
      message = 'Check Internet Connection';
      result = false;
    }

    if(response && response['status'] == 401){
      console.log(response);
      message = '';
      message = 'Unauthorized Access, You cannot perform this action.';
      result = false;
    }
    
    return {result:result,message:message};
  }
  // formatError(response) {
  //   let message: any = [];
  //   let result;
  //   console.log(response);
  //   if (response['status'] == 200) {
  //     if (response['error'] && response['error']['message'] && response['error']['message'].isArray) {
  //       result = false;
  //       let errMessage = response['error']['message'];
  //       for (let obj in errMessage) {
  //         for (let objc of errMessage[obj]) {
  //           message.push(objc);
  //         }
  //       }
  //     } else if (response['error'] && response['error']['message']) {
  //       result = false;
  //       message.push(response['error']['message']);
  //     }

  //     if (response['error'] && response['error']['error']) {
  //       message.push(response['error']['error']['message']);
  //       result = false;
  //     }

  //     if (response['message'] && !response['error']) {
  //       message.push(response['message']);
  //       result = true;
  //     }
  //   } else {
  //     if (response['message'] && response['error']) {
  //       message = '';
  //       message = 'Internal Server Error';
  //       result = false;
  //     }
  //   }
  //   console.info(message);
  //   return { result: result, message: message };
  // }

}



